import instance from './axios_settings';
import authHeader from './auth-header';
import { authService } from './auth.service';
import store from '../store/store';

async function makeRequest(method, url, data = null, useAuth = true, retry = true) {
    console.log('makeRequest');
    const config = {
        method: method,
        url: url,
        headers: useAuth ? authHeader() : { 'ngrok-skip-browser-warning': '69420' },
        data: data,
    };

    try {
        const response = await instance.request(config);
        console.log('makeRequest запрос прошел');
        return response.data;

    } catch(error) {
        console.log('makeRequest запрос не прошел');
        if(error.response) {
            if(useAuth && error.response.status === 401) {
                if(retry) {
                    await authService.refresh();
                    return await makeRequest(method, url, data, true, false);
                } else {
                    store.dispatch('auth/logout', true)
                }
                
            } else if ([500, 408, 429, 502].includes(error.response.status)) {
                throw {response: { data: 'Сервер не отвечает, попробуйте позднее...', status: 500 }};
            } else {
                throw error;
            }
        } else {
            console.log('makeRequest запрос не прошел сервер отсутствует', error);
            throw {response: { data: 'Сервер не отвечает, попробуйте позднее...', status: 500 }};
        }
    }
}

export default makeRequest;